.elementor-section {
	overflow: hidden;
}
.clt-review-slider {
	position: relative;

	.review {
		padding: 1.875rem 0;

		.review-content {
			padding: 0 2.5rem;
			border-left: solid 1px $color-border;
			border-right: solid 1px $color-border;
			text-align: center;
		}

		.active-star {
			color: $color-primary;
		}

		.message {
			font-size: 1.125rem;
			margin-bottom: 1.25rem;
		}

		.author {
			font-weight: 500;
			opacity: 0.3;
		}
	}

	.swiper-pagination {
		width: 100%;
		display: flex;
		justify-content: center;
		gap: 20px;
	}

	.clt-review-swiper-button-next,
	.clt-review-swiper-button-prev {
		border: 1px solid #dee0ea;
    border-radius: 50%;
    color: #000;
    height: 30px;
    position: absolute;
    top: 40%;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
		z-index: 99;

		&::after {
			display: none;
		}
	}

	.clt-review-swiper-button-prev{
		left: 5px;
	}

	.clt-review-swiper-button-next{
		right: 5px;
	}

	@include media-breakpoint-down(lg) {
		.swiper-button-next,
		.swiper-button-prev {
			display: none;
		}
	}
}
