.clt-banner-slider {
  overflow: hidden;
  position: relative;

  &:hover{
    .banner-slider-swiper-button-next,
    .banner-slider-swiper-button-prev {
      display: flex;
    }
  }

  .banner-slider-swiper-button-next,
  .banner-slider-swiper-button-prev {
    background-color: #fff;
    font-size: 1.4375rem;
    height: 3.5rem;
    top: calc(50% - 32px);
    position: absolute;
    transition: opacity .2s cubic-bezier(.28, .12, .22, 1);
    width: 2rem;
    z-index: 99;
    align-items: center;
    justify-content: center;
    display: none;
  }

  .banner-slider-swiper-button-next {
    right: 0px;
  }

  .banner-slider-swiper-button-prev {
    left: 0px;
  }

  .banner-slider-item {
    overflow: hidden;
    position: relative;
    min-height: 1px;
    float: left;
    backface-visibility: hidden;

    .banner-content {
      justify-content: flex-start;
    }

    .banner-image {
      // position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }

    .entry-subtitle {
      font-size: 0.625rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 1px;
      margin-bottom: 0.625rem;
    }

    .entry-title {
      font-size: 3.125rem;
      font-weight: 300;
      line-height: 1.1;
      margin-bottom: 1.25rem;
    }
  }
}