.widget_recently_viewed_products {
  .widget-title {
    font-size: 1rem;
    font-weight: 500;
  }

  ul.product_list_widget li {
    margin-bottom: 5px;

    img {
      float: left;
      margin-right: 20px;
      width: 55px;
      height: 60px;
    }

    .product-title {
      font-size: 0.875rem;
      font-weight: 400;
      color: #000;
    }

    del,
    ins {
      font-size: 0.8125rem;
    }
  }
}

// Dng cho widget elementor item
.clt-recently-viewed-products-item {
  .title {
    font-size: .9375rem;
    text-align: center;
    margin: 1rem 0;
  }

  .price {
    display: flex;
    gap: 10px;
    justify-content: center;

    ins{
      font-weight: 500 !important;
      text-decoration: none;
    }
  }
}

.clt-recently-viewed-products__swiper{
  position: relative;

  .recently-viewed-products-swiper-button-next,
  .recently-viewed-products-swiper-button-prev {
    position: absolute;
    top: calc(50% - 50px);
    z-index: 9;
    font-size: 35px;
  }

  .recently-viewed-products-swiper-button-next{
    right: 0;
  }
}