.clt-category-slider {
	.slide-header {
		display: flex;
		align-items: baseline;
		flex-flow: row wrap;
		line-height: 1.6;
		margin-bottom: 2.5rem;

		.entry-title {
			font-size: 1.375rem;
			margin-bottom: 0;
			margin-right: 0.9375rem;
		}

		p {
			font-size: 0.875rem;
			margin-bottom: 0;
			color: #a6a7ac;
		}
	}

	.category-swiper-container {
		position: relative;

		.swiper-button-next {
			right: -5%;
		}

		.swiper-button-prev {
			left: -5%;
		}

		.swiper-button-next,
		.swiper-button-prev {
			top: calc(50% - 1.15vw - 0.65vh);
			background-image: none;
		}
	}
	.clt-category__swiper {
		overflow-x: hidden;
		height: fit-content;

		.swiper-wrapper {
			display: flex;
			align-items: center;
		}

		.category {
			.category-thumbnail {
				overflow: hidden;

				&:hover {
					.total-product {
						opacity: 1;
						z-index: 30;
					}
				}

				img {
					width: 100%;
					height: auto;
					object-fit: cover;
					transition: transform 0.1s linear;

					&:hover {
						transform: scale(1.1);
					}
				}

				.total-product {
					position: absolute;
					font-size: 0.6875rem;
					font-weight: 600;
					top: 0.625rem;
					left: 0.625rem;
					padding: 2px 6px;
					background-color: #fff;
					opacity: 0;
					-webkit-transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
					transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
				}
			}

			.category-content {
				text-align: center;
				a {
					white-space: nowrap;
					font-size: 1rem;
					font-weight: 500;
				}
			}
		}
	}
}
