.clt-shop-page {
  .widget_price_filter {
  margin-bottom: 2.5rem;

    .widget-title {
      font-size: 1rem;
      font-weight: 500;
      margin-bottom: 1.25rem;
    }

    .ui-slider {
      .ui-slider-range {
        background-color: $color-primary;
      }

      .ui-slider-handle {
        background-color: $color-primary;
        width: 0.8em;
        height: 0.8em;
        top: -0.4em;
      }
    }

    .ui-slider-horizontal {
      height: 2px;
    }

    .price_slider_wrapper {
      .ui-widget-content {
        background-color: #fafafa;
      }
    }

    .price_slider_wrapper {
      position: relative;
      display: flex;
      flex-direction: column-reverse;

      .price_slider_amount {
        align-items: center;
        display: flex;
        flex-direction: row-reverse;
        margin-bottom: 1.875rem;
      }
    }

    .button{
      font-size: 0.6875rem !important;
      text-transform: uppercase;
      padding: 0 0.75rem;
      height: 2.125rem;
      background-color: #f2f3f5;
      border-color: transparent;
      color: $color-text;
    }

    .price_label{
      position: absolute;
      left: 0;
    }
  }
}