.clt-banner-box {
  &.dark {
    color: $color-text;
  }

  .banner-content {
    justify-content: flex-start;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding: 1.75rem 1.875rem;
    z-index: 2;

    .entry-subtitle{
      font-size: 1.254rem;
    }

    .entry-title{
      font-size: 1.354rem;
    }

  }

  .entry-description {
    p {
      font-size: 1rem;
      margin-bottom: 1.25rem;
    }
  }

  .content-middle {
    .banner-content {
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }
  }

  .banner-image{
    img{
      object-fit: cover;
      width: 100%;
    }
  }

  .banner-box-href{
    color: inherit;
    display: block;
  }
}