.clt-widget-offered-product {

	.product-content {
		border: 2px solid $color-border;
		padding: 1.25rem;
		transition: border-color 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);

		&:hover {
			border: 2px solid $color-primary;

			.group-product-button a {
				border: none;
				opacity: 1;
				transform: translateX(-15px);
			}

			.count-item{
				background-color: $color-primary;
				color: #fff;
			}
		}
	}

	.thumbnail-wrapper {
		position: relative;
	}

	.product-title {
		a {
			font-size: 1.125rem;
		}
	}

	.content-wrapper .product-price {
		font-size: 1.125rem !important;

		ins {
			color: $color-primary;
		}
	}

	.product-rating {
		margin-top: 1.25rem;
		margin-bottom: 0.9375rem;
		position: relative;
		display: flex;
		flex-flow: row wrap;
		align-items: center;
	}

	.star-rating {
		position: relative;
		font-size: 12px;
		width: 68px;
		overflow: hidden;

		&::before {
			font-family: "klbtheme";
			content: "\e83b\e83b\e83b\e83b\e83b";
			letter-spacing: 3px;
			color: #ddd;
		}

		span {
			position: absolute;
			top: 0;
			left: 0;
			overflow: hidden;

			&::before {
				font-family: "klbtheme";
				content: "\e83b\e83b\e83b\e83b\e83b";
				letter-spacing: 3px;
				color: #f9d900;
			}
		}
	}

	.count-rating {
		font-size: 0.8125rem;
		font-weight: 500;
		margin-left: 0.625rem;
	}

	.entry-content {
		font-size: 0.875rem;
		margin-bottom: 1rem;
	}

	.sold-product p {
		font-size: 0.8125rem;
		color: #a6a7ac;
	}

	.countdown {
		align-items: center;
		margin-bottom: 0.9375rem;

		.count-item {
			width: 2.25rem;
			height: 2.375rem;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			font-size: 0.875rem;
			font-weight: 500;
			background-color: #eff0f3;
			border-radius: 2px;
			transition: background-color 0.2s cubic-bezier(0.28, 0.12, 0.22, 1), color 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
		}

		.count-label {
			display: none;
		}
	}

	// Thubmnail
	.product-badges {
		left: 1.625rem;
		position: absolute;
		top: 0.625rem;
		z-index: 3;

		.badge {
			align-items: center;
			background-color: #fff;
			border-radius: 0.25rem;
			color: #47b486;
			display: inline-block;
			font-size: .75em;
			font-weight: 700;
			height: 1.3125rem;
			line-height: 1;
			padding: 0.35em 0.65em;
			text-align: center;
			vertical-align: baseline;
			white-space: nowrap;
		}
	}

	.group-product-button {
		a {
			align-items: center;
			background-color: #fff;
			border-radius: 50%;
			color: currentColor;
			display: inline-flex;
			height: 2.125rem;
			justify-content: center;
			opacity: 0;
			transform: translateX(0px);
			transition: background-color .1s cubic-bezier(.28, .12, .22, 1), color .1s cubic-bezier(.28, .12, .22, 1), opacity .3s cubic-bezier(.28, .12, .22, 1), transform .3s cubic-bezier(.28, .12, .22, 1), -webkit-transform .3s cubic-bezier(.28, .12, .22, 1);
			width: 2.125rem;
			will-change: opacity, transform;
		}
	}

	.added_to_cart.wc-forward {
		overflow: hidden;

		&::before {
			color: #000;
			content: "\e81d";
			display: flex;
			font-family: klbtheme;
			height: 20px;
			left: 0;
			margin-left: 52px;
			margin-top: 3px;
			padding-bottom: 30px;
			padding-right: 13px;
			top: 0;
		}
	}
}