.widget_product_categories {
  margin-bottom: 2.5rem;

  .widget-title {
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 1.25rem;
  }

  a {
    color: #000;
    font-size: 14px;
    display: block;

    &::before {
      border: 1px solid #dee0ea;
      border-radius: 2px;
      color: #fff;
      content: "";
      font-size: 10px;
      margin-right: 15px;
      opacity: 1;
      padding: 0 7px;
      position: relative;
    }
  }

  ul {
    padding-left: 0;

    li {
      align-items: flex-start;
      display: inherit;
      flex-flow: row wrap;
      margin-top: 8px;
      position: relative;
    }
  }

  .cat-parent {
    .children {
      padding-left: 30px;
    }

    &>subDropdown {
      &::after {
        color: #636363;
        font-weight: 100;
        content: "+";
        cursor: pointer;
        float: right;
        font-size: 18px;
        left: 0.5px;
        line-height: 1;
        opacity: 1;
        position: relative;
      }
    }

    .children {
      display: none;

    }
  }

  .current-cat {
    &>a {
      color: $color-primary;
      font-size: 14px;
      display: block;
      font-weight: 500;

      &::before {
        color: #fff;
        border: 1px solid $color-primary;
        border-radius: 2px;
        content: "";
        font-size: 9px;
        margin-right: 15px;
        opacity: 1;
        padding: 0 3px;
        position: relative;
        background-color: $color-primary;
      }
    }

    // .children {
    //   display: block;
    // }

    &.cat-parent {
      .children {
        display: block;
        .cat-parent{
          .children{
            display: none;
          }
        }
      }

    }

  }

  .cat-parent{
    &.current-cat-parent{
      .children {
        display: block;
        .cat-parent{
          .children{
            display: none;
          }
        }

        .current-cat-parent{
          .children{
            display: block;

            .cat-parent{
              display: block;
            }
          }
        }

        .current-cat.cat-parent{
          .children{
            display: block;
          }
        }
      }

      .cat-parent{
        .children{
          display: block;
        }
      }
    }
  }

  .subDropdown {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    text-align: center;



    &.plus {
      &::before {
        content: "+";
      }
    }

    &.minus {
      &::before {
        content: "-";
      }
    }
  }
}