.woocommerce-widget-layered-nav {
  margin-bottom: 2.5rem;

  .widget-title{
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 1.25rem;
  }
}

// Color
ul.woocommerce-widget-layered-nav-list.pa_color{
  li.wc-layered-nav-term{
    margin: 8px 0;
  }

  span.color-box{
    width: 20px;
    height: 20px;
    position: relative;
    display: inline-block;
    border-radius: 50%;
    background-repeat: no-repeat;
    vertical-align: middle;
    margin-right: 10px;
    background-color: #9e9e9e;
    border: 1px solid $color-form-active;
    color: #fff;
  }

  a {
    color: #000;
    font-size: 14px;
    padding-right: 20px;
    display: inline-flex;
    vertical-align: middle;

    &::before {
      display: none;
    }
  }

  span.count {
    float: right;
    font-size: 0.8125rem;
    line-height: 25px;
    color: #9aa5b3;
  }

  .widget-title {
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 1.25rem;
  }

  .chosen {
    a {
      color: $color-primary;
      font-weight: 500;

      &::before {
        color: $color-primary;
      }
    }

    span.color-box{
      &::before {
        content: '\2713';
        margin-left: 4px;
      }
    }
  }
}

// Size
ul.woocommerce-widget-layered-nav-list.pa_size{
  li.wc-layered-nav-term{
    margin: 6px 0;
  }

  a {
    color: #000;
    font-size: 14px;
    padding-right: 20px;

    &::before {
      border: 1px solid #dee0ea;
      border-radius: 2px;
      color: #fff;
      content: "";
      font-size: 10px;
      margin-right: 15px;
      opacity: 1;
      padding: 0 7px;
      position: relative;
    }
  }

  span.count {
    float: right;
    font-size: 0.8125rem;
    line-height: 25px;
    color: #9aa5b3;
  }

  .widget-title {
    font-size: 1rem !important;
    font-weight: 500;
    margin-bottom: 1.25rem;
  }

  .chosen {
    &>a {
      color: $color-primary;
      font-size: 14px;
      font-weight: 500;

      &::before {
        background-color: #ee403d;
        border: 1px solid #ee403d;
        color: #fff;
        content: "";
        padding: 2px 3px;
        margin-right: 15px;
      }
    }
  }
}