.clt-coupon-text-banner {

	.module-coupon-banner {
		width: 100%;
		height: 100%;
	}

	a.overlay-link {
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
	}

	.module-body {
		display: flex;
		flex-direction: row;
		align-items: center;

		@include media-breakpoint-down(md) {
			& {
				flex-direction: column;
			}
		}
	}

	.module-coupon-banner .module-body {
		p {
			font-size: 1.25rem;
			font-weight: 500 !important;
			text-align: center;
		}

		span.clt-text-banner-subtitle {
			font-weight: 300;
		}

		.discount-code {
			margin-bottom: 0;
			margin-left: 0.75rem;
			margin-right: 0.75rem;
			font-size: 0.875rem;
			font-weight: 500 !important;
			text-transform: uppercase;
			border: 1px dashed;
			padding: 4px 12px;
		}
	}

}