.clt-post-slider__swiper {
	position: relative;

	.post-slider-swiper-button-next,
	.post-slider-swiper-button-prev {
		position: absolute;
		top: calc(50% - 20px);
		z-index: 9;

		&::before{
			font-size: 30px;
		}
	}

	.post-slider-swiper-button-next{
		right: 0px;
	}
}

.post-slider-item {
	.post-title {
		font-size: 1.375rem;
		margin-bottom: 0.625rem;
		margin-top: 0.625rem;
		font-weight: 500;

		a {
			color: $color-text;
		}
	}

	.post-detail{
		margin-top: 15px;
		text-align: center;
	}
}