// loop product content
.clt-products__swiper {

  .content-wrapper.left {
    .product-review {
      justify-content: left;
    }

    .product-title {
      text-align: left !important;
    }

    .product-price {
      text-align: left !important;
      ;
      display: block;
    }
  }

  .content-wrapper.center {
    .product-review {
      justify-content: center;
    }

    .product-title {
      text-align: center !important;
      height: 43px;

    }

    .product-price {
      text-align: center;
      display: block;
    }
  }

  .content-wrapper.right {
    .product-review {
      justify-content: right;
    }

    .product-title {
      text-align: right !important;
    }

    .product-price {
      text-align: right !important;
      ;
      display: block;
    }
  }

  .product-badges {
    position: absolute;
    top: 0.625rem;
    left: 0.625rem;
    z-index: 3;

    .badge {
      align-items: center;
      background-color: #fff;
      border-radius: 0.25rem;
      color: $color-success;
      display: inline-block;
      font-size: .75em;
      font-weight: 700;
      height: 1.3125rem;
      line-height: 1;
      padding: 0.35em 0.65em;
      text-align: center;
      vertical-align: baseline;
      white-space: nowrap;
    }
  }

  ul.products li.product .onsale {
    left: 0;
    margin: auto;
    min-height: auto;
    min-width: 4.25em;
  }

  .product-carousel-swiper-button-next,
  .product-carousel-swiper-button-prev {
    position: absolute;
    top: 37%;
    width: 27px;
    margin-top: -22px;
    z-index: 10;
    cursor: pointer;
    background-size: 27px 44px;
    background: no-repeat 50%;

    &::before {
      font-size: 30px;
    }
  }

  .product-carousel-swiper-button-next {
    right: 15px;
  }
}

.thumbnail-wrapper {
  margin-bottom: 1.25rem;
  position: relative;

  //Button group
  &:hover {
    .group-product-button {
      a, .add_to_cart_button {
        transform: translateX(0);
        opacity: 1;
        border: none;

        &:nth-child(2) {
          transition-delay: 0.16s;
        }

        &:nth-child(3) {
          transition-delay: 0.20s;
        }
      }

      .loading {
        opacity: 1 !important;
      }
    }
  }

  .group-product-button {
    flex-direction: column;
    top: 0.625rem;
    right: 0.625rem;
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    z-index: 3;


    .add_to_wishlist_button.addedwl {
      i {
        &::before {
          content: '\e839';
        }
      }
    }

    // n elemnt woo sinh ra
    .woocommerce-LoopProduct-link,
    .added_to_cart {
      display: none;
    }

    .button {
      margin-bottom: 0.5em !important;
      margin-top: 0px !important;

      &:hover {
        background-color: $color-primary;
        color: #fff;
      }

      i {
        font-size: 1.125rem;
        margin-left: 0;
        position: relative;
        top: 0;
      }
    }

    .loading {
      display: flex;
      padding-right: 0 !important;
      align-items: center;
      justify-content: center;
      opacity: 0 !important;

      &::after {
        -webkit-animation: spin 1s linear infinite;
        opacity: 1;
        animation: spin 1s linear infinite !important;
        position: absolute;
        top: 50% !important;
        left: 24%;
        margin-top: -9px;
        transition: opacity .2s ease;
        content: "" !important;
        display: inline-block;
        width: 18px;
        height: 18px;
        border: 1px solid rgb(161 161 161 / 40%);
        border-left-color: #fff;
        border-radius: 50%;
        vertical-align: middle;
      }

      i {
        display: none;
      }
    }

    .added {
      &::after {
        margin-left: 0 !important;
        content: "\e844";
        display: inline-block;
        font-family: klbtheme;
        font-size: 16px;
        font-weight: 400;
      }

      i {
        display: none;
      }
    }

    a, .add_to_cart_button, .variable-button {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 2.125rem;
      height: 2.125rem;
      color: currentColor;
      background-color: #fff;
      border-radius: 50%;
      transition: background-color 0.1s cubic-bezier(.28, .12, .22, 1), color 0.1s cubic-bezier(.28, .12, .22, 1), opacity 0.3s cubic-bezier(.28, .12, .22, 1), transform 0.3s cubic-bezier(.28, .12, .22, 1), -webkit-transform 0.3s cubic-bezier(.28, .12, .22, 1);

      -webkit-transform: translateX(10px);
      transform: translateX(10px);
      will-change: opacity, transform;
      opacity: 0;
    }

    a.woosc-btn:before {
      font-family: "klbtheme";
      font-style: normal;
      font-weight: 400;
      display: inline-block;
      text-decoration: inherit;
      width: 1em;
      margin-right: 0.2em;
      text-align: center;
      font-variant: normal;
      text-transform: none;
      line-height: 1em;
      margin-left: 0.2em;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: "\e865";
      font-size: 1.0625rem;
    }
  }

  .product-carousel-image {
    .hover-slider-images-toggler {
      position: absolute;
      inset: 0px;
      display: flex;
    }
  }

  // Image slide
  .product-carousel-image {
    position: relative;
    display: block;

    .hover-slider-indicator {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 3px;
      bottom: -6px;
      left: 0;
      right: 0;
    }

    .hover-slider-indicator-dot {
      flex: 1;
      height: 4px;
      background-color: rgba(0, 0, 0, 0.1);
    }

    .active {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }
}

.content-wrapper {
  position: relative;
  padding: 0 5px;

  .product-review {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    justify-content: center;
    margin-bottom: 10px;

    .star {
      font-size: 0.875rem;
      margin-right: 0.5rem;
      color: #f9c300;
    }

    span {
      font-size: 0.8125rem;
      font-weight: 500;
    }
  }

  .product-title {
    font-size: 0.9375rem !important;
    font-weight: 400;
    height: 43px;

    a {
      color: currentColor;
      text-decoration: none;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      display: -webkit-box;
      font-weight: 400;
      letter-spacing: 0;
      line-height: 1.43;
      margin-bottom: 10px;
      overflow: hidden;
    }
  }

  .product-price {
    color: $color-text !important;
    font-size: 0.9375rem !important;
    font-weight: 500 !important;

    del {
      color: inherit;
      font-size: 94%;
      font-weight: 400;
      margin-right: 3px;
      opacity: .3;
    }

    ins {
      font-weight: 500 !important;
      text-decoration: none;
    }
  }

  .hover-slider-init {
    img {
      width: 100%;
    }
  }
}
