.module-counter-product {
	position: relative;
	height: fit-content !important;

	.module-body {
		position: absolute;
		background-color: transparent;
		top: 0;
		left: 0;

		.container .row {
			justify-content: end;
		}
	}

	.module-image {
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			object-fit: cover;
		}
	}
	.content-wrapper {
		max-width: 70%;
	}
	.counter-product-content {
		position: relative;

		.entry-subtitle {
			font-size: 0.75rem;
			font-weight: 600;
			letter-spacing: 1px;
			margin-bottom: 0.5rem;
		}

		.entry-title {
			font-size: 2.875rem;
			font-weight: 300;
			margin-bottom: 1.25rem;
		}

		.entry-description {
			font-size: 1rem;
			font-weight: 300;
		}

		.btn {
			font-size: 1rem;
			width: auto;
			height: 2.7rem;
			margin-top: 1rem;
			padding-left: 1.875rem;
			padding-right: 1.875rem;
		}

		.countdown {
			width: fit-content;
			margin-top: 1rem;
			display: flex;
			align-items: center;
			gap: 7px;

			.count-item {
				&,
				div {
					display: flex;
				}
				height: 100%;
				align-items: baseline;
				font-size: 2.125rem;
				font-weight: 300;
			}

			.count-label {
				font-size: 55%;
				margin-left: 4px;
				font-weight: 300;
				display: flex;
				align-items: center;
			}
		}

		.expired-text {
			margin-top: 1rem;

			p {
				font-size: 0.8125rem;
				font-weight: 300;
				margin-bottom: 0;
			}
		}
	}

	@media only screen and (max-width: 1025px) {
		.hide-mobile {
			display: none;
		}

		.counter-product-content {
			left: 0 !important;
			bottom: 0 !important;
		}

		.module-body {
			position: relative;

			.content-wrapper {
				max-width: 100%;
				width: 100%;
			}
		}

		.counter-product-content {
			z-index: 1;
			position: relative;
			padding: 2.5rem 0.9375rem;

			.entry-title {
				font-size: 1.875rem;
				font-weight: 500;
				margin-bottom: 1.25rem;
			}

			.btn {
				width: 100%;
			}
		}
	}
}
